.wrapper{
    /*position: absolute;*/
     /*width: 800px;*/
     top: 0px;
    left: 0px;
}
.crop-wrapper{
    position: relative;
    background-color: #FFF;
    float:left;
    /*height: 800px;*/
}
.resize-container{
    position: absolute;
    display: inline-block;
    cursor: move;
    margin: 0 auto;
    margin-left: 2px;
}

.resize-container img {
    display: block;
}
.resize-container:hover img,
.resize-container:active img{
    outline: 2px dashed rgba(222, 60, 80, .9)
}

.resize-handle-ne,
.resize-handle-se,
.resize-handle-nw,
.resize-handle-sw{
    position: absolute;
    display: block;
    width: 10px;
    height: 10px;
    background: rgba(222,60,80,.9);
    z-index: 999
}

.resize-handle-nw{
    top: -5px;
    left: -5px;
    cursor: nw-resize;
}
.resize-handle-sw{
    bottom: -5px;
    left: -5px;
    cursor: sw-resize;
}
.resize-handle-ne{
    top: -5px;
    right: -5px;
    cursor: ne-resize;
}
.resize-handle-se{
    bottom: -5px;
    right: -5px;
    cursor: se-resize;
}



.overlay {
    position: absolute;
     /*top:50%;
    left: 50%;*/
    /*margin-left: -100px;
    margin-top: -100px;*/
    /*transform: translate(-50%, -50%);*/
    z-index: 999;
    /*width: 200px;
    height: 200px;*/
    border: solid 2px rgba(222, 60, 80, .9);
    box-sizing: content-box;
    pointer-events: none;
}
.overlay:after,
.overlay:before{
    content: '';
    position: absolute;
    display: block;
    height: 40px;
    border-left: dashed 2px rgba(222, 60, 80, .9);
    border-right: dashed 2px rgba(222, 60, 80, .9);
}

.overlay:before{
    top: 0;
    margin-left: -2px;
    margin-top: -40px;
}

.overlay:after{
    bottom: 0;
    margin-left: -2px;
    margin-bottom: -40px;
}

.overlay-inner:after,
.overlay-inner:before{
    content: '';
    position:  absolute;
    display: block;
    width: 40px;
    border-top: dashed 2px rgba(222, 60, 80, .9);
    border-bottom: dashed 2px rgba(222, 60, 80, .9);
}

.overlay-inner:before{
    left: 0;
    margin-left: -40px;
    margin-top: -2px;
}
.overlay-inner:after{
    right: 0;
    margin-right: -40px;
    margin-top: -2px;
}

.btn-crop{
    z-index: 999;
    background-color: rgba(222, 60, 80);
    border: none;
    border-radius: 5px;
}
.btn-exit{
    z-index: 999;
    background-color: rgba(222, 60, 80);
    border: none;
    border-radius: 5px;
}

.clear{
    clear: both
}


